    border-top-right-radius: 15px;
    width: 200px;
    
}
  
.check-list-title{
    background-color: black;
    font-size: 20px;
    text-align: center;
    font-family: 'Source Sans Pro', sans-serif;
    
}

.check-list {
    display: flex;
    font-family: 'Source Sans Pro', sans-serif;
    flex-direction: column;
    margin: 0;
    padding: 10px;
    border-top: 2px solid black;
    background: white;
    max-width: 650px;
}

.check-list-closed {
    display: none;
}

.check-list-input[type=checkbox] {
    margin: 4px;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    color: #363839;
    border: 1px solid #bdc1c6;
    border-radius: 4px;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    outline: 0;
    cursor: pointer;
    transition: background 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
}
.check-list-input[type=checkbox]::before {
    position: absolute;
    content: "";
    display: block;
    top: 2px;
    left: 7px;
    width: 8px;
    height: 14px;
    border-style: solid;
    border-color: #fff;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    opacity: 0;
}
.check-list-input[type=checkbox]:checked {
    color: #fff;
    border-color: #06842c;
    background: #06842c;
}
.check-list-input[type=checkbox]:checked::before {
    opacity: 1;
}
.check-list-input[type=checkbox]:checked ~ label::before {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}

.check-list-label {
    color: black;
    position: relative;
    cursor: pointer;
    font-size: 20px;;
    font-weight: 600;
    padding: 0 0.25em 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    margin : 0px !important
}
.check-list-label::before {
    position: absolute;
    content: attr(data-content);
    color: #9c9e9f;
    -webkit-clip-path: polygon(0 0, 0 0, 0% 100%, 0 100%);
            clip-path: polygon(0 0, 0 0, 0% 100%, 0 100%);
    text-decoration: line-through;
    text-decoration-thickness: 3px;
    -webkit-text-decoration-color: #363839;
            text-decoration-color: #363839;
    transition: -webkit-clip-path 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
    transition: clip-path 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
    transition: clip-path 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94), -webkit-clip-path 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

